/* ----- Base ----- */
html {
  height: 100%;
  text-align: center;
  width: 100%;
}

body {
  margin: 0 auto;
  min-height: 100vh;
  display: flex;
  width: 100%;
  align-items: center;
  background: whitesmoke;
}

/* ----- Wrapper ----- */
.wrapper {
  margin: 0 auto;
  max-height: 80vh;
  width: 100%;
  max-width: 1280px;
  display: flex;
  flex-direction: row;
  border: 2px solid black;
}

main {
  display: flex;
  padding: 48px;
  text-align: left;
  background: white;
  width: 100%;
}

/* ----- Pages ----- */
.page {
  display: none;
  min-height: calc(100vh - 128px);
  padding: 64px 0;
  animation: fadeIn 0.3s;
}

/* ----- Nav-Tabs ----- */
.nav-links {
  background-color: lightgoldenrodyellow;
  display: flex;
  flex-direction: column;
  gap: 48px;
  text-align: left;
  padding: 0 32px;
  min-width: 180px;
  height: 100%;
  justify-content: center;
}
.nav-links a {
  color: black;
  text-decoration: none;
  font-size: 18px;
}
.nav-links a:hover {
  background-color: black;
  color: white;
}
.nav-links a.active {
  border-bottom: 2px solid black;
}

/* ----- animations ----- */
/* fade in */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.hide {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s 0.4s, opacity 0.4s linear;
}